import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Selecionar fecha de entrega';
  options = {
      weekday: "long",
      year: "numeric",
      month: "short",
      day: "numeric"
  };
  dia00 : Date = new Date();
  dia01 : Date = new Date();
  dia02 : Date = new Date();
  dia03 : Date = new Date();
  dia04 : Date = new Date();
  dia05 : Date = new Date();
  dia06 : Date = new Date();

  dia0 = 'HOY ' + this.dia00.toLocaleDateString("es",this.options).toUpperCase();
  dia1 = new Date(this.dia01.setTime( this.dia01.getTime() + 1 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
  dia2 = new Date(this.dia01.setTime( this.dia02.getTime() + 2 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
  dia3 = new Date(this.dia01.setTime( this.dia03.getTime() + 3 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
  dia4 = new Date(this.dia01.setTime( this.dia04.getTime() + 4 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
  dia5 = new Date(this.dia01.setTime( this.dia05.getTime() + 5 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
  dia6 = new Date(this.dia01.setTime( this.dia06.getTime() + 6 * 86400000 )).toLocaleDateString("es",this.options).toUpperCase();
}



